import { useState, useEffect } from 'react';
import { Searchbar } from './Searchbar/Searchbar';
import { ImageGallery } from './ImageGallery/ImageGallery';
import { Loader } from './Loader/Loader';
import { Button } from './Button/Button';

import { Container,  StyledImage, Notification } from "./styled";

import { Notify } from 'notiflix/build/notiflix-notify-aio';
import placeholderImg from './placeholder-image.png';
import api from '../services/pixabay-api';


export const App = () => {

  const [query, setQuery] = useState('');
  const [images, setImages] = useState([]);
  const [pageNumber, setPageNumber] = useState(1);
  const [loading, setLoading] = useState(false);
  const [noMatch, setNoMatch] = useState(false);          //    : 
  const [collectionEnd, setCollectionEnd] = useState(false);


  const getImages = async (query, pageNumber) => {     //  -   
    setLoading(true); 
    try {
        const { data } = await api.fetchImages(query, pageNumber);   //     
        const filteredApiResponse = data.hits.map(({ id, webformatURL, largeImageURL, tags }) => ({ id, webformatURL, largeImageURL, tags })); //shorthand;    ,  4
        
        if(data.hits.length === 0) {    // data.hits -  ',    
            return setNoMatch(true);
        }

        setImages(prevState => ([...prevState, ...filteredApiResponse]));

        if(pageNumber !== 1) {
          setTimeout(() => {
            window.scrollBy({
              top: 510,
              behavior: 'smooth',
          });
          }, 150);
        }
      
        if(pageNumber >= Math.ceil(data.totalHits / 12)) {
            Notify.warning("You've reached the end of search results."); 
            setCollectionEnd(true);
        }
    } catch(error) {
      Notify.warning(error.message); 
    } finally {
        setLoading(false); 
    }
  }


  useEffect(() => {
    if(!query) {
      return;
    }    
    try {
      getImages(query, pageNumber); //  -
    } catch(error) {
      Notify.warning(error.message); 
    }
  }, [query, pageNumber]);


  const handleFormSubmit = query => {  //  Searchbar  ,     <input>
    setQuery(query);
    setImages([]);
    setPageNumber(1);
    setNoMatch(false);          //    : 
    setCollectionEnd(false);
  }
  

  const handleBtnClick = () => {
      setPageNumber(prevState => prevState + 1);   
  } 

  return (
    <Container>
      <Searchbar onSubmit={handleFormSubmit}/>       {/* onSubmit -  ,   */}
        
      {images.length !== 0 && <ImageGallery images={images} />}
      { loading && <Loader /> }

      {noMatch && //     
        <>
            <StyledImage src={placeholderImg} alt='man on the moon' />
            <Notification>No images corresponding your request '{query}' found in our Universe. Try searching for another term.</Notification>
        </>
      }

      {(images.length !== 0 && !collectionEnd) && <Button onClick={handleBtnClick}/> } {/*    */}
    </Container> 
  );
}