import { useEffect } from "react";            
import { createPortal } from "react-dom";                   
import PropTypes from 'prop-types';
import { StyledOverlay, StyledModal } from "./styled";

const modalRoot = document.querySelector('#modal-root');


export const Modal = ({ largeImageURL, tags, onClose }) => {    

    useEffect(() => {
        const handleKeyDown = event => { 
            if(event.code === 'Escape') {  //   Escape
                onClose();
            }
        }
        window.addEventListener('keydown', handleKeyDown);

        return () => {
            window.removeEventListener('keydown', handleKeyDown); //    (componentWillUnmount),    
        }
    }, [onClose]); // useEffect   1-     onClose 

    
    const handleBackdropClick = event => {  //    
        if(event.currentTarget === event.target) {
            onClose();  //  useEffect -  return
        }
    }

    return createPortal(
        <StyledOverlay onClick={handleBackdropClick}>
            <StyledModal>
                <img src={largeImageURL} alt={tags} width='900'/>
            </StyledModal>
        </StyledOverlay>,
        modalRoot,
    );
}

Modal.propTypes = {
    largeImageURL: PropTypes.string.isRequired,
    tags: PropTypes.string.isRequired,
    onClose: PropTypes.func.isRequired,
};